.pop {
	width: 100%;
	height: 100vh;
	background: rgba(0, 0, 0, 0.9);
	padding: 5vw;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 5;
	.con {
		width: 100%;
		height: 100%;
		border: 0.0625rem solid #555;
		cursor: pointer;
		iframe {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.close {
		font: bold 0.875rem/1 "arial";
		color: #d83a3a;
		position: absolute;
		top: 2.5rem;
		right: 5vw;
		cursor: pointer;
	}
}
