.Location {
	#map {
		width: 100%;
		height: 31.25rem;
		background: #000;
	}
	.btnSet {
		display: flex;
		flex-wrap: wrap;
		align-content: flex-start;
		justify-content: space-between;
		margin-top: 3.125rem;

		button {
			cursor: pointer;
		}

		ul {
			display: flex;
			li {
				min-width: 10rem;
				margin: 0 0.625rem;
				cursor: pointer;
				position: relative;
				padding: 0.625rem 1.25rem;
				color: #333;
				background: transparent;
				border: none;
				font-size: 1rem;
				text-align: center;
				letter-spacing: -0.0625rem;
				text-decoration: none;
				text-transform: uppercase;
				overflow: hidden;
				transition: 0.5s;
				height: 3rem;
				line-height: 1.5625rem;
				font-weight: bold !important;
				background: #333;
				color: #fff;
				border-radius: 0.3125rem;
				&:hover {
					box-shadow: inset 0 0 0 2em #fff;
					outline: 0.0625rem solid #333;
					color: #333;
				}
				&.on {
					background: #d83a3a;
					border: 0.0625rem solid #d83a3a;
					color: #fff;
					&:hover {
						box-shadow: inset 0 0 0 2em #fff;
						outline: 0.0625rem solid #333;
						color: #333;
					}
				}
			}
		}
	}
}
