.content {
	padding-bottom: 3%;
	background: #fff;
	figure {
		margin-top: 5% !important;
		margin-bottom: 2% !important;
		padding-top: 3% !important;
	}
	> .inner {
		width: 73.75rem;
		min-height: 25rem;
		margin: 1.875rem auto 0rem;
		position: relative;
		opacity: 0;
		transform: translateY(6.25rem);
		//transition: opacity 0.5s, transform 1s;
		transition: 0.5s;
		h1 {
			text-align: center;
			color: #333;
			// font-family: "Montserrat", Sans-serif;
			font-size: 4.6875rem;
			font-weight: 500;
			margin-bottom: 3.4375rem;
			&::after {
				// content: "";
				display: block;
				width: 37.5rem;
				height: 3.1875rem;
				background: #555;
				margin: 0 auto;
				background: url(../../../public/img/underline.png);
				background-size: cover;
			}
		}
	}
	&.on {
		figure {
			opacity: 1;
		}
		> .inner {
			opacity: 1;
			transform: translateY(0rem);
		}
	}
}

@media screen and (max-width: $tablet) {
	.content {
		figure {
			height: 12.5rem;
		}
		.inner {
			width: 100%;
			margin: 5rem auto 7.5rem;
			padding: 0rem 2.5rem;
		}
	}
}

.com-type01 {
	position: relative;
	transform: translateX(-7.1875rem) translateY(7.1875rem);
	background: #fff;
	padding: 6.25rem 7.5rem 7.5rem 7.5rem;
	.num {
		position: absolute;
		top: -3.6875rem;
		right: 5rem;
		color: #4a4a4a;
		font-family: "proxima_nova_rgregular", serif !important;
		font-weight: 100;
		font-size: 6rem;
		line-height: 1.5;
		display: flex;
		align-items: center;
		&::before {
			content: "";
			display: inline-block;
			position: relative;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			width: 43.75rem;
			margin-right: 1.25rem;
			height: 0.0625rem;
			background: #333;
		}
	}
	h2 {
		font-family: "freight-big-pro", serif !important;
		font-size: 2.8125rem;
		line-height: 1.3;
		color: #0b3a29;
		font-weight: 100;
	}
	h3 {
		margin-top: 2.5rem;
		font-family: "proxima_nova_rgregular" !important;
		color: #073534 !important;
		font-size: 1.2rem !important;
		line-height: 1.6;
		font-weight: 300;
	}
	a {
		display: inline-block;
		margin-top: 2.5rem;
		color: #8b572a;
		font-style: normal;
		font-weight: 100;
		font-size: 1.8rem;
		letter-spacing: 0rem !important;
		opacity: 0.8;
		font-family: "freight-big-pro", serif;
	}
	&.typ2 {
		.num {
			display: flex;
			align-items: flex-start;
			top: -5.0625rem;
			left: 0;
			right: auto;
			&::before {
				left: 3vw;
				width: 0.0625rem;
				height: 46.0625rem;
				top: 7.3125rem;
				transform: initial;
				margin-top: 3.3125rem;
				margin-right: 0;
			}
		}
	}
	&.typ3 {
		.num {
			display: flex;
			align-items: flex-start;
			top: -17.875rem;
			left: auto;
			right: 1rem;
			flex-direction: column;
			&::before {
				left: 3vw;
				width: 0.0625rem;
				height: 46.0625rem;
				top: -5.625rem;
				transform: initial;
				margin-top: 3.3125rem;
				margin-right: 0;
			}
		}
	}
}
