.Youtube {
	.section-wrap {
		display: flex;
		flex-wrap: wrap;
		gap: 1.25rem;
	}
	article {
		width: calc(25% - 0.9375rem);
		border-top: 0.0625rem solid #aaa;
		background: #fff;
		border: 0.3125rem solid #333;
		padding: 0.625rem;
		// padding: 1.25rem 0rem;
		// display: flex;
		// flex-wrap: wrap;
		justify-content: space-between;
		img {
			width: 100%;
		}
		h3 {
			font: normal 1.25rem/1.2 "arial";
			width: 100%;
			overflow: hidden;
			text-overflow: ellipsis;
			font-weight: bold;
			margin-bottom: 0.625rem;
			color: #333;
			margin-top: 0.625rem;
			margin-bottom: 0.625rem;
		}
		.txt {
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			align-content: space-between;
			p {
				font: 0.8125rem/1.3 "arial";
				color: #999;
				filter: saturate(0);
				margin-bottom: 1.125rem;
				max-height: 1.875rem;
				overflow: hidden;
			}
			span {
				font: 0.8125rem/1 "arial";
				color: #d83a3a;
			}
		}
		.pic {
			width: 100%;
			height: 12.5rem;
			background: #000;
			// filter: saturate(0);
			transition: 0.5s;
			img {
				width: 100%;
				height: 100%;
			}
			&:hover {
				filter: saturate(1.2);
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	.Youtube {
		article {
			h3 {
				width: 48%;
			}
			.txt {
				width: 50%;
			}
			.pic {
				width: 100%;
				margin-top: 3.125rem;
				margin-bottom: 5rem;
			}
		}
	}
}

@media screen and (max-width: $mobile) {
	.Youtube {
		article {
			h3 {
				width: 100%;
				margin-bottom: 1.25rem;
			}
			.txt {
				width: 100%;
			}
			.pic {
				height: 7.5rem;
				margin-bottom: 0rem;
			}
		}
	}
}
