.Gallery {
	.loading {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		z-index: 0;
	}
	.controls {
		display: flex;
		justify-content: space-between;
		align-items: center;
		nav {
			padding: 0.625rem;
			display: flex;
			gap: 1.25rem;
		}

		.searchBox {
			padding-right: 0.9375rem;
			input {
				margin-right: 0.625rem;
				width: 12.5rem;
				height: 3rem;
				border: 0.125rem solid #333;
				padding: 0.1875rem 0.5rem;
				color: #333;
				::placeholder {
					color: #333;
					font-weight: bold;
				}
			}
		}
	}

	.frame {
		transform: translateY(12.5rem);
		opacity: 0;
		transition: all 0.5s;
		&.on {
			transform: translateY(0rem);
			opacity: 1;
		}
		&::after {
			content: "";
			display: block;
			clear: both;
		}
		article {
			width: 25%;
			padding: 0.625rem;
			float: left;
			//masonryë¼ëŠ” ì»´í¬ë„ŒíŠ¸ë¥¼ ì´ìš©í•  ì˜ˆì •ì´ë¼ floatì„ ì“´ë‹¤

			.inner {
				width: 100%;
				padding: 0.625rem;
				background: #fff;
				border: 0.3125rem solid #333;
				box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.03);
				cursor: pointer;
				height: 20rem;
				margin-bottom: 0.9375rem;
				.pic {
					width: 100%;
					margin-bottom: 0.3125rem;
					img {
						width: 100%;
						height: 11.25rem;
						object-fit: cover;
					}
				}
				h2 {
					font: normal 1.25rem/1.2 "arial";
					width: 100%;
					overflow: hidden;
					text-overflow: ellipsis;
					font-weight: bold;
					margin-bottom: 0.625rem;
					color: #333;
					margin-top: 0.625rem;
					margin-bottom: 0.3125rem;
				}
				.profile {
					display: flex;
					align-items: center;
					img {
						width: 2.5rem;
						border-radius: 50%;
						margin-right: 0.625rem;
					}
					span {
						color: #d83a3a;
						cursor: pointer;
						transition: 0.5s;
						margin-top: 0.625rem;
						font-size: 0.9375rem;
						word-break: break-all;

						&:hover {
							color: aqua;
						}
					}
				}
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	section .frame article {
		width: 33.3333%;
	}
}

@media screen and (max-width: $mobile) {
	section .frame article {
		width: 100%;
	}
}

.load-4 {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 12.5rem;
	left: 50%;
	transform: translateX(-50%);
}
.load-4 .ring-1 {
	animation: loadingD 1.5s 0.3s cubic-bezier(0.17, 0.37, 0.43, 0.67) infinite;
}
.ring-1 {
	width: 3.125rem;
	height: 3.125rem;
	margin: 0 auto;
	padding: 3.125rem;
	border: 0.4375rem dashed #d83a3a;
	border-radius: 100%;
}
@keyframes loadingD {
	0% {
		transform: rotate(0deg);
	}
	50% {
		transform: rotate(180deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
