footer {
	width: 100%;
	border-top: 0.0625rem solid #bbb;
	padding: 6.25rem 0rem;
	background: #303030;
	.inner {
		width: 73.75rem;
		margin: 0rem auto;
		position: relative;
		p {
			text-align: center;
			font: 0.75rem/1 "arial";
			color: #999;
			letter-spacing: 0.125rem;
		}
	}
}

@media screen and (max-width: $tablet) {
	footer {
		.inner {
			width: 100%;
			padding: 0rem 2.5rem;
		}
	}
}
