.Department {
	p {
		text-align: center;
		color: #333;
		font-family: "Raleway", Sans-serif;
		font-size: 1.375rem;
		font-weight: 600;
		margin-bottom: 1.25rem;
	}
	H2 {
		text-align: center;
		color: #333;
		font-family: "Raleway", Sans-serif;
		font-size: 1.125rem;
		font-weight: 500;
		margin-bottom: 2.875rem;
	}
	h3 {
		text-align: center;
		color: #333;
		font-weight: bold;
	}
	p.name {
		text-align: center;
		color: #333;
		font-family: "Noto Sans KR", Sans-serif;
		font-size: 1rem;
		font-weight: 400;
	}
	.img {
		margin-bottom: 2.1875rem;
		img {
			width: 100%;
			height: 22rem;
		}
	}
	> .inner {
		&::after {
			content: "";
			display: block;
			clear: both;
		}
		article {
			width: 33.333%;
			float: left;
			padding: 0.625rem;
			margin-bottom: 3.125rem;
			.inner {
				width: 100%;
				.pic {
					width: 100%;
					height: 18.75rem;
					background: #111;
					margin-bottom: 1.25rem;
					filter: saturate(0.2);
					transition: 0.5s;
					&:hover {
						filter: saturate(1.1);
						img {
							filter: grayscale(0);
						}
					}
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
						filter: grayscale(1);
					}
				}
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	.Department {
		> .inner {
			article {
				width: 50%;
				margin-bottom: 2.5rem;
				.inner {
					.pic {
						height: 16.25rem;
					}
				}
			}
		}
	}
}

@media screen and (max-width: $mobile) {
	.Department {
		> .inner {
			article {
				width: 100%;
				margin-bottom: 1.875rem;
				.inner {
					.pic {
						height: 12.5rem;
					}
				}
			}
		}
	}
}
