header {
	width: 100%;
	position: absolute;
	z-index: 2;
	left: 0;
	right: 0;
	width: 100%;
	top: 0;
	.inner {
		position: relative;
		width: 73.125rem;
		height: 6.25rem;
		margin: 0 auto;

		h1 {
			position: absolute;
			bottom: 0.625rem;
			left: 0rem;
			a {
				font: bold 1.5rem/1 "arial";
				color: #fff;
			}
			img {
				position: relative;
				left: -2.25rem;
			}
		}
		#gnb {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: 0rem;
			li {
				float: left;
				margin-right: 2.1875rem;
				&:last-of-type {
					margin-right: 0;
				}
				a {
					display: block;
					text-transform: uppercase;
					position: relative;
					&:hover {
						&::after {
							content: "";
							display: inline-block;
							position: absolute;
							left: 0;
							width: 100%;
							top: 1.875rem;
							background: #000;
							height: 0.125rem;
							background: #d83a3a;
						}
					}
				}
			}
		}
		.fa-bars {
			font-size: 1.5rem;
			position: absolute;
			bottom: 1.25rem;
			right: 2.5rem;
			display: none;
		}
	}
	&.main {
		background-color: #191919;
		height: 6.25rem;
		padding: 0.625rem;
		transition: 0.5s height, 0.5s padding;
		display: flex;
		align-items: center;
		justify-content: space-between;
		.inner {
			h1 a {
				color: #fff;
			}
			#gnb li a {
				display: block;
				font-size: 0.9375rem;
				font-weight: 500;
				color: #fff !important;
				font-family: "Montserrat", sans-serif;
			}
			.fa-bars {
				color: #fff;
			}
		}
	}
	&.sub {
		background-color: #191919;
		height: 6.25rem;
		padding: 0.625rem;
		transition: 0.5s height, 0.5s padding;
		display: flex;
		align-items: center;
		justify-content: space-between;
		.inner {
			h1 {
				img {
					// filter: invert(1);
				}
			}
			h1 a {
				color: #333;
			}
			#gnb li a {
				display: block;
				font-size: 0.9375rem;
				font-weight: 500;
				color: #fff !important;
				font-family: "Montserrat" !important;
			}
			.fa-bars {
				color: #333;
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	header {
		width: 100%;
		.inner {
			width: 100%;
			h1 {
				bottom: 1.25rem;
				left: 2.5rem;
			}
			#gnb {
				display: none;
			}
			.fa-bars {
				display: block;
			}
		}
	}
}
