.Member {
	.inner form table th {
		color: #333;
		font-size: 2.0625rem !important;
		font-weight: 700;
		text-transform: uppercase;
		position: relative;
	}
	> .inner {
		h1 {
		}
		form {
			border-radius: 0.625rem;
			box-shadow: 0.3125rem 0.3125rem 0.3125rem rgba(0, 0, 0, 0.1);
			padding: 1.875rem 1.25rem;
			border: 0.3125rem solid #333;
			table {
				width: 100%;
				tr {
					width: 100%;
				}
				th,
				td {
					display: block;
					width: 100%;
					&.flex {
						display: flex;
						label {
							padding-left: 0.625rem;
						}
					}
					input {
						display: block;
						width: 100%;
						border: none;
						outline: none;
						outline: none;
						border-bottom: 0.125rem solid #333;
						background-color: transparent;
						padding: 0.9375rem 0.9375rem;
					}
					.err {
						font-size: 0.8125rem;
						color: #d83a3a;
						display: inline-block;
						margin-top: 0.3125rem;
						padding-left: 0.9375rem;
					}
					input[type="checkbox"],
					input[type="radio"] {
						width: auto;
						margin: 0 0.625rem;
					}
					select {
						margin-top: 0.3125rem;
						width: 100%;
						border: 0.0625rem solid #aaa;
						padding: 0.5rem 0.9375rem;
					}
				}
				th {
					padding: 0 0 0.125rem 0.625rem;
					color: #333 !important;
					font-family: "Roboto", Helvetica, sans-serif;
					font-size: 1em;
					color: #888;
					font-weight: 500;
					text-align: left;
					label,
					.Member > .inner form table th {
						color: #333;
						font-size: 2.0625rem;
						font-weight: 700;
						text-transform: uppercase;
						position: relative;
					}
				}
				td {
					margin-bottom: 1.875rem;
				}
			}
			textarea {
				width: 100%;
				min-height: 6.25rem;
			}
			input[type="reset"] {
				cursor: pointer;
				padding: 1.125rem 0.9375rem;
				background-color: #d83a3a;
				color: #fff;
				border-bottom: transparent;
			}
			input[type="submit"] {
				cursor: pointer;
				padding: 0.625rem 0.9375rem;
				background-color: #333;
				color: #fff;
				border-bottom: transparent;
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	.Member {
		> .inner {
			table {
				td {
					input[type="text"],
					input[type="password"],
					select,
					textarea {
						width: 80%;
					}
					.err {
						display: block;
						margin-top: 0.625rem;
					}
				}
			}
		}
	}
}

@media screen and (max-width: $mobile) {
	.Member {
		> .inner {
			table {
				th {
					display: none;
					padding: 0.9375rem 0rem;
					&:last-child {
						display: block;
						width: 100%;
					}
					input[type="reset"],
					input[type="submit"] {
						width: 48%;
						margin: 0rem;
					}
				}
				td {
					border-left: none;
					padding: 0.9375rem 0rem;
					input[type="text"],
					input[type="password"],
					select,
					textarea {
						width: 100%;
					}
				}
			}
		}
	}
}
